function CompensationInputValidator(event) {
    const target = event.target;

    if (target.validity.valueMissing||target.value==="") {
        if (target.name === "birthdate") {
            target.setCustomValidity("Gimimo data yra privalomas laukelis")

        } else if (target.name === "phone"||target.name === "kindergartenPhone") {
            target.setCustomValidity("Telefono numeris yra privalomas laukelis")

        } else if (target.name === "kindergartenAccountNumber") {
            target.setCustomValidity("Banko sąskaitos numeris yra privalomas laukelis")

        } else {
            target.setCustomValidity(target.placeholder + " yra privalomas laukelis");
        }

    } else if (target.name === "email"||target.name === "kindergartenEmail") {
      
        if (target.validity.patternMismatch) {
            target.setCustomValidity("Neteisingas el. pašto formatas")
        } else {
            target.setCustomValidity("")
        }
    } else if (target.name === "personalCode" || target.name === "childPersonalCode") {
        if (target.validity.patternMismatch) {
            target.setCustomValidity("Asmens koda sudaro 11 skaičių, įvesta skaičių: " + target.value.length)
        } else {
            target.setCustomValidity("")
        }
    } else if (target.name === "name" || target.name === "childName") {
        if (target.validity.patternMismatch) {
            target.setCustomValidity("Netinkamo formato vardas")
        } else {
            target.setCustomValidity("")
        }
    } else if (target.name === "surname" || target.name === "childSurname") {
        if (target.validity.patternMismatch) {
            target.setCustomValidity("Netinkamo formato pavardė")
        } else {
            target.setCustomValidity("")
        }
      
    } else if (target.name === "address"||target.name === "kindergartenAddress") {

        target.setCustomValidity("");
    } else if (target.name === "phone"||target.name === "kindergartenPhone") {
        if (target.value.includes('+')) {
            if (target.validity.patternMismatch) {
                target.setCustomValidity("Telefono numerį sudaro nuo 4 iki 19 skaičių, įvesta skaičių: " + (0 + target.value.length - 1))
            }
            else {
                target.setCustomValidity("");
            }
        } else {
            target.setCustomValidity('Formatas: +37000000000')
        }
    } else if (target.name === "kindergartenBankCode") {
        console.log("target.placeholder");
        console.log(target.placeholder);
        if (target.validity.patternMismatch) {
            console.log("mismatch");
            target.setCustomValidity("Banko kodas turi susidaryti iš 5 skaitmenų, įvesta skaičių: " + (0 + target.value.length));
        } else {
            console.log("match");
            target.setCustomValidity("");
        }
    }else if (target.name === "kindergartenName"||target.name === "kindergartenBankName") {
        if (target.validity.patternMismatch) {
            target.setCustomValidity("Netinkamo formato pavadinimas")
        } else {
            target.setCustomValidity("")
        }
    }else if (target.name === "kindergartenCode") {
        if (target.validity.patternMismatch) {
            target.setCustomValidity("Įstaigos kodas turi būti sudarytas iš 2-20 skaičių")
        } else {
            target.setCustomValidity("")
        }
    }else if (target.name === "kindergartenAccountNumber") {
        if (target.validity.patternMismatch) {
            target.setCustomValidity("Neatitinka formato LT00-0000-0000-0000-0000")
        } else {
            target.setCustomValidity("")
        }
    }else{
        target.setCustomValidity("")
    }
}

export default CompensationInputValidator;

